<template>
  <div>
    <navi></navi>
    <div class="center">
      <el-input placeholder="请在此处搜索网点名称" :suffix-icon="icon" prefix-icon="el-icon-search" @clickIcon="aa" v-model="greeting">
      </el-input>
    </div>
    <ld-card :mode="mode" v-if="link_text" :trade="trade" :adress="adress" :traffichub="traffichub" :privatesea="privatesea" :newadress="newadress" @change="change"></ld-card>
    <ld-card v-if="link_text" :trade="trade" :adress="adress" :traffichub="traffichub" :privatesea="privatesea" :newadress="newadress"></ld-card>
    <ld-card v-if="link_text" :trade="trade" :adress="adress" :traffichub="traffichub" :privatesea="privatesea" :newadress="newadress"></ld-card>
    <div>
      <img class="img_add" src="/static/imgs/icon_add.png" @click="newAdd" alt="">
    </div>
    <el-dialog :visible.sync="modal" :top="'20vh'" :width="'284px'" :show-close="false" center>
      <div slot="title">
        添加成功
        <p class="tips">
          【某某某某酒店（某分店）】已成功 添加到你的似海，请在
          <span style="color:red">7天之内</span>完成 入驻
        </p>
      </div>
      <div slot="footer">
        <span class="cancel" @click="goprivate">
          去私海
        </span>
        <span class="comfirm" @click="closepop">知道了</span>
      </div>
    </el-dialog>
      <el-dialog :visible.sync="applyreview" :show-close="false" :top="20+'vh'" :width="'284px'" center>
      <div slot="title">
        <span class="tip_header"> 已经提交审核流程 </span>
        <p class="tips">
          流程提交成功，请在CRM系统-我的申请中查看，审批通过后会自动添加 到你的私海
        </p>
      </div>
      <span id="dialog_body"></span>
      <div slot="footer">

        <span class="comfirm" @click="closeapply">知道了</span>
      </div>
      <!--流程提交成功，请在CRM系统-我的-->
      <!--申请中查看，审批通过后会自动添加-->
      <!--到你的私海-->
    </el-dialog>
  </div>

</template>

<script>
import eBus from "../eBus";
export default {
  data() {
    return {
      greeting: "",
      trade: "某某某某某餐厅（某某某分店）",
      traffichub: "交通枢纽/飞机场",
      adress:
        "广东省深圳市南山区南湖路3009号国贸商厦2A2H（富苑皇冠假日酒店正对面）",
      privatesea: "该网点已存在：自由网点，可添加到私海",
      newadress: false,
      header: "新建网点",
      link_text: true,
      span_text: false,
      mode: "1",
      icon: "",
      applyreview:false,
      modal: true
    };
  },
  methods: {
    aa() {},
    change() {
      this.$router.push("/public/publicdetails");
    },
    newAdd() {
      this.$router.push("/public/newpoint2");
    },
    goprivate() {
      this.$router.push("/private/settled");
    },
    closepop() {
      this.modal = false;
    },
    closeapply(){
      this.applyreview=false;
    }
  },
  created() {
    eBus.$on("addprivate", () => {
      this.modal = true;
    });
  },
  watch: {
    greeting(val) {
      if (!!val) {
        this.icon = "el-icon-circle-close";
      } else {
        this.icon = "";
      }
    }
  }
};
</script>
<style lang="scss" scoped>
* {
}
.img_add {
  width: 54px;
  height: 54px;
  position: fixed;
  right: 20px;
  bottom: 40px;
  background: #ffffff;
  border-radius: 100%;
}

.center {
  //   width: 100%;
  height: 60px;
  display: flex;
  padding-left: 20px;
  padding-right: 19px;
  align-items: center;
  justify-content: center;
  background: #f7f7f7;
}

.tips {
  font-family: ".PingFangSC-Regular";
  font-size: 14px;
  color: #999999;
  letter-spacing: 0;
  text-align: left;
}
</style>
<style lang="scss" >
.el-dialog__body {
  padding: 0px;
  color: #444;
  line-height: 24px;
  font-size: 14px;
}
</style>
